// CookiePowerCalc.js
import React, { useState } from 'react';
import styled from 'styled-components';
import ArtifactsPopup from './powercalc/ArtifactsPopup'; // Import the new ArtifactsPopup component

const PageContainer = styled.div`
  display: flex;
  padding: 20px;
`;

const Column = styled.div`
  flex: 1;
  margin: 0 10px;
  text-align: center;
  cursor: pointer;
`;

const SelectedItem = styled.div`
  margin-top: 20px;
`;

const CookiePowerCalc = () => {
  const [showPopup, setShowPopup] = useState(false);
  const [selectedArtifact, setSelectedArtifact] = useState(null);

  const handleOpenPopup = () => {
    setShowPopup(true);
  };

  const handleClosePopup = () => {
    setShowPopup(false);
  };

  const handleSelectArtifact = (artifact) => {
    setSelectedArtifact(artifact);
    setShowPopup(false);
  };

  return (
    <PageContainer>
      <Column onClick={handleOpenPopup}>
        <h2>Artifacts</h2>
        <SelectedItem>
          {selectedArtifact ? (
            <img src={selectedArtifact.image} alt={selectedArtifact.name} width="80" />
          ) : (
            'Select Artifact'
          )}
        </SelectedItem>
      </Column>
      <Column>
        <h2>Cookies</h2>
        <SelectedItem>Select Cookie</SelectedItem>
      </Column>
      <Column>
        <h2>Equipment</h2>
        <SelectedItem>Select Equipment</SelectedItem>
      </Column>

      {showPopup && <ArtifactsPopup onClose={handleClosePopup} onSelect={handleSelectArtifact} />}
    </PageContainer>
  );
};

export default CookiePowerCalc;
